<template>
  <div style="height: auto; overflow: hidden; padding-bottom: 200px">
    <i-form label-position="top" class="newtec-form-barcode">
      <form-item label=":">
        <i-input
          v-model="barcodeContent"
          type="textarea"
          :rows="2"
          @on-blur="onBarcodeChange"
        ></i-input>
      </form-item>

      <form-item label=":">
        <i-input
          v-model="width"
          placeholder=""
          :min="1"
          type="number"
          @on-change="onBarcodeChange"
        ></i-input>
      </form-item>

      <form-item label=":">
        <i-input
          v-model="height"
          placeholder=""
          :min="1"
          type="number"
          @on-change="onBarcodeChange"
        ></i-input>
      </form-item>

      <form-item label=":">
        <i-input
          v-model="lineColor"
          size="small"
          style="width: 111px"
          @on-change="onBarcodeChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="lineColor"
              :editable="false"
              alpha
              :transfer="true"
              size="small"
              @on-change="onBarcodeChange"
            />
          </span>
        </i-input>
      </form-item>

      <form-item label=":">
        <i-input
          v-model="background"
          size="small"
          style="width: 111px"
          @on-change="onBarcodeChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="background"
              :editable="false"
              alpha
              :transfer="true"
              size="small"
              @on-change="onBarcodeChange"
            />
          </span>
        </i-input>
      </form-item>

      <form-item label=":" class="newtec-form-horizontal">
        <div>
          <i-switch v-model="displayValue" @on-change="onBarcodeChange" />
        </div>
      </form-item>

      <template v-if="displayValue">
        <form-item label=":">
          <i-input
            v-model="text"
            placeholder=""
            @on-blur="onBarcodeChange"
          ></i-input>
        </form-item>

        <form-item label=":">
          <i-select
            v-model="textPosition"
            placeholder=""
            @on-change="onBarcodeChange"
          >
            <i-option value="bottom"></i-option>
            <i-option value="top"></i-option>
          </i-select>
        </form-item>

        <form-item label=":">
          <i-select
            v-model="textAlign"
            placeholder=""
            @on-change="onBarcodeChange"
          >
            <i-option value="center"></i-option>
            <i-option value="left"></i-option>
            <i-option value="right"></i-option>
          </i-select>
        </form-item>

        <form-item label=":">
          <i-input
            v-model="fontSize"
            placeholder=""
            :min="1"
            type="number"
            @on-change="onBarcodeChange"
          ></i-input>
        </form-item>

        <form-item label=":">
          <i-select
            v-model="fontOptions"
            placeholder=""
            @on-change="onBarcodeChange"
          >
            <i-option value="bold"></i-option>
            <i-option value="italic"></i-option>
            <i-option value="bold italic">&</i-option>
          </i-select>
        </form-item>
      </template>
    </i-form>
  </div>
</template>
<script>
export default {
  name: 'bar-code-setting',
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {
        return {
          barcodeContent: "",
          background: "#fff",
          lineColor: "#000",
          width: "",
          height: "",
          displayValue: false,
          text: "",
          textPosition: "bottom",
          textAlign: "center",
          fontSize: "",
          fontOptions: "",
        };
      },
    },
  },
  data() {
    return {};
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.resetForm();
      },
    },
  },
  methods: {
    validateBarcode: (value) => {
      let reg = /^[a-zA-Z0-9]+$/;
      let reg2 = /^.{4,18}$/;
      // 418
      if (value !== "" && !reg.test(value)) {
        Vue.prototype.$Message.error("");
        return false;
      }
      return true;
    },
    resetForm: function () {
      if (this.settings) {
        Object.keys(this.settings).map((k) => {
          this[k] = this.settings[k];
        });
      }
    },
    onBarcodeChange() {
      if (this.validateBarcode(this.barcodeContent)) {
        let obj = {};
        Object.keys(this.settings).map((k) => {
          obj[k] = this[k];
        });
        //
        obj.background = this.background ? obj.background : "#ffffff";
        obj.lineColor = this.lineColor ? obj.lineColor : "#000000";
        this.$emit("change", obj);
      }
    },
  },
};
</script>
<style scoped>
.newtec-form-barcode .ivu-form-item-label {
  padding-bottom: 2px !important;
}
.newtec-form-barcode .ivu-switch:after {
  height: 16px;
}

.newtec-form-barcode .ivu-switch {
  height: 20px;
  line-height: 20px;
}
.newtec-form-barcode .ivu-color-picker .ivu-select-dropdown {
  left: -142px !important;
}
.newtec-form-horizontal .ivu-form-item-content {
  margin-left: 100px;
}

.newtec-form-horizontal .ivu-form-item-label {
  text-align: left;
  width: 100px;
  float: left;
  padding: 10px 12px 10px 0;
}
</style>